<style lang="stylus">
  .text
    font-size 20px
</style>

<template>
  <div id="saveArticle">
    <Form :model="form" :label-width="80">
      <FormItem label="">
        <Upload action="/api/admin/file/upload" :show-upload-list="false"
                :on-success="handleSuccess"
                :on-error="handleErr"
                :before-upload="beforeUpload"
                :on-format-error="handleFormatError"

                :format="['jpg','jpeg','png']">
          <Button type="ghost" icon="ios-cloud-upload-outline"></Button>
        </Upload>
        <img :src="form.Img" style="max-height: 240px">
        <div>
          <span class="text"></span><span class="text">{{form.Img}}</span>
        </div>
        <div>
          <span class="text">MarkDown</span><span class="text">![img]({{form.Img}})</span>
        </div>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        form: {
          Img: ''
        }
      }
    },
    methods: {
      handleSuccess (res) {
        if (res.status === 10000) {
          this.form.Img = 'http://123.207.39.128:8080' + res.filepath
          this.$Message.success('')
        } else {
          this.$Message.error(',')
        }
      },
      handleErr () {
        this.$Message.error('')
      },
      beforeUpload () {
      },
      handleFormatError () {
        this.$Message.error(', jpg  png ')
      }
    }
  }
</script>




